import React, { memo, useState } from 'react'
import { SPACING } from '../../../constants'
import { normalize } from '../../../utils'
import { ScrollView, View, FlatList } from 'react-native'
import HoverableView from '../../HoverableView'
import { TouchableRipple } from 'react-native-paper'
import RenderImage from '../../list/RenderImage'

const PhotosList = ({ onImagePress, images }) => {
    const [currentImage, setCurrentImage] = useState(images[0])

    return (
        <ScrollView contentContainerStyle={{ padding: SPACING.medium, paddingBottom: 0, width: normalize(800), maxWidth: '100%', alignSelf: 'center' }}>
            <View style={{ flexDirection: 'row', flexWrap: 'wrap' }}>
                {images.map((photo, index) => (
                    <HoverableView key={photo} hoveredOpacity={0.8} style={{ width: '50%', marginBottom: SPACING.medium, paddingHorizontal: SPACING.small }}>
                        <TouchableRipple onPress={() => onImagePress(index)} style={{ flex: 1 }}>
                            <RenderImage image={photo} />
                        </TouchableRipple>
                    </HoverableView>
                ))}
            </View>
        </ScrollView>
    )

    return (
        <View style={{ flex: 1, alignItems: 'center', marginHorizontal: SPACING.large, marginVertical: SPACING.medium }}>
            <View style={{ width: 500, maxWidth: '90%' }}>
                <RenderImage image={currentImage} />
            </View>
        </View>
    )
}

export default memo(PhotosList)